body {
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a:link { color: rgb(3, 3, 3); text-decoration: none; }
a:visited { color: rgb(2, 2, 2); text-decoration: none; }
a:hover { color: rgb(45, 35, 173); text-decoration: none; }
a:active { color: rgb(45, 35, 173); text-decoration: none;}